@import 'utils/variables', 'utils/mixins';

.link {
  display: flex;
  align-items: center;
  gap: 40px;
  padding: 8px 0;
}

.body {
  flex-grow: 1;
}

.common {
  position: relative;
  display: flex;

  &__link {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 8px;
    color: $blue-2;
    fill: $blue-2;
    font-size: 16px;
    font-weight: 700;

    @include darkTheme {
      color: $middle-blue;
      fill: $middle-blue;
    }

    &:hover {
      filter: brightness(0.8);
    }
  }

  &__icon {
    line-height: 0;
  }
}

.details {
  background-color: white;
  box-shadow: 0px 6px 18px 0px rgba(9, 16, 28, 0.13), 0px 0px 4px 0px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  padding: 8px 48px 8px 8px;
  position: absolute;
  left: -8px;
  top: calc(100% + 2px);
  z-index: 1;
  position: absolute;
  font-size: 16px;

  @include darkTheme {
    background-color: $dark-bg-3;
  }

  &__close {
    all: unset;
    cursor: pointer;
    position: absolute;
    right: 8px;
    top: 8px;
    display: flex;
    align-items: center;
    justify-content: center;

    & > svg {
      display: block;
      transform: scale(0.8);

      path {
        @include darkTheme {
          fill: $dark-bg-4;
        }
      }
    }
  }

  &__label {
    font-weight: 700;
    margin-bottom: 8px;
  }

  &__all {
    color: $blue-2;
    cursor: pointer;
    text-decoration: underline;
    margin-top: 4px;

    &:hover {
      text-decoration: none;
    }
  }
}

.icon {
  padding: 4px;
  line-height: 0;
  cursor: pointer;

  svg {
    fill: $blue-2;

    @include darkTheme {
      fill: $middle-blue;
    }
  }

  &:hover {
    filter: brightness(0.8);
  }
}
