@import '../../../styles/utils/variables', '../../../styles/utils/mixins';

.container {
  width: 1082px;
  max-width: 100%;
  min-height: calc(100vh - 210px);
}

.search {
  padding: 16px 24px 0 24px;
}

.actions {
  &__line {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    line-height: 24px;
    padding: 16px 40px;

    &:not(:last-child) {
      border-bottom: 1px solid $base-gray;

      @include darkTheme {
        border-color: $light-black-3;
      }
    }
  }
}

.export {
  color: $green;
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;

  svg {
    fill: $green;
  }

  &:hover {
    filter: brightness(0.8);
  }
}

.loader {
  margin-top: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.checkbox {
  font-size: 14px;
  line-height: 24px;
}

.noResults {
  font-size: 14px;
  padding: 16px 40px;
  text-align: center;
}

.types {
  padding: 0 24px;
}

.bottomBar {
  display: flex;
  justify-content: space-between;
}
