@import 'utils/variables', 'utils/mixins';

$data-margin-top: 7px;
$data-margin-left-right: 10px;

.wrapper {
  cursor: pointer;
  width: 100%;
  display: flex;
  align-items: center;
  transition: all 0.1s ease-in-out;
  will-change: background-color, height;
  padding: 12px 16px;
  gap: 32px;
  font-size: 14px;
  line-height: 24px;

  &:not(:last-child) {
    border-bottom: 1px solid $base-gray;

    @include darkTheme {
      border-color: $dark-bg-3;
    }
  }

  &:hover {
    background-color: $light-gray;
  }

  @include darkTheme {
    background-color: $dark-bg-2;

    &:hover {
      background-color: $black-blue-light;
    }
  }
}

.main {
  margin-top: $data-margin-top;
  margin-bottom: $data-margin-top;
  margin-left: $data-margin-left-right;

  max-width: 50%;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.additional {
  margin-top: $data-margin-top;
  margin-bottom: $data-margin-top;
  margin-right: $data-margin-left-right;

  max-width: 60%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  animation-name: fadeIn;
  animation-duration: 0.2s;
  animation-fill-mode: forwards;
}

.additionalData {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
}

.additionalDataContainer {
  position: relative;

  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;

  &:not(:first-child) {
    margin-top: 5px;
  }
}

.dataName {
  width: 120px;
  font-size: 12px;
  line-height: 12px;
  color: $blue-grey;

  &:not(:first-child) {
    margin-top: 2px;
  }

  &:last-child {
    margin-bottom: 2px;
  }

  @include darkTheme {
    color: $white;
  }
}

.dataValue {
  max-width: 137px;

  font-size: 11.5px;
  line-height: 12px;
  color: $blue-grey;
  font-weight: 400;

  &:last-child {
    margin-top: 1px;
  }

  @include darkTheme {
    color: $white;
  }
}

.additionalButtonContainer {
  margin-top: 4px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.additionalButtonLabel {
  font-size: 11px;
  line-height: 15px;
  color: $dark-blue;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.additionalInfoIcon {
  margin-left: 6px;
  height: 9px;
  width: 9px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
