@import 'utils/variables', 'utils/mixins';

.container {
  position: absolute;
  top: calc(100vh / 2 - 70px);
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 4;
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 300px;
  height: 170px;

  background: #FFFFFF;
  box-shadow: 0px 0px 30px rgba(42, 47, 66, 0.2);
  border-radius: 8px;

  @include respond-to('large') {
    width: 260px;
    height: 160px;
  }

  @include respond-to('medium') {
    width: 250px;
    height: 150px;
  }
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  margin-top: 5px;
  margin-bottom: 15px;
  font-weight: normal;

  color: #2A2F42;

  @include respond-to('large') {
    font-size: 17px;
  }

  @include respond-to('medium') {
    font-size: 15px;
    margin-top: 5px;
  }
}

.text {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  line-height: 25px;
  font-weight: normal;

  color: #2A2F42;

  @include respond-to('large') {
    font-size: 17px;
  }

  @include respond-to('medium') {
    font-size: 15px;
    line-height: 20px;
  }
}