@import 'utils/variables', 'utils/mixins';

.accordion {
  &.level {
    &_2 {
      &:not(:last-child) {
        & > .header {
          border-bottom: 1px solid $base-gray;

          @include darkTheme {
            border-color: $light-black-3;
          }

          &.isOpen {
            border-bottom: none;
          }
        }
      }
    }
  }
}

.header {
  user-select: none;
  display: flex;
  align-items: center;
  padding: 16px;
  gap: 16px;
  font-size: 14px;
  line-height: 24px;

  &.level {
    &_1 {
      &.isOpen {
        border-bottom: 1px solid $base-gray;

        @include darkTheme {
          border-color: $light-black-3;
        }
      }
    }

    &_2 {
      padding-left: 56px;
    }

    &_3 {
      padding: 16px 0;

      &.isOpen {
        padding: 16px 0 8px 0;
      }
    }
  }
}

.arrow {
  line-height: 0;
  cursor: pointer;
  transition: all 150ms;
  fill: $base-middle-gray;

  svg {
    transition: all 150ms;
    transform: rotate(180deg);

    @include darkTheme {
      fill: $dark-white;
    }
  }

  &.isOpen {
    svg {
      transform: rotate(0deg);
    }
  }
}

.name {
  flex-grow: 1;
  cursor: pointer;

  &.level {
    &_1,
    &_3 {
      font-size: 16px;
      font-weight: 700;
    }
  }
}

.openAll {
  display: flex;
  align-items: center;
  gap: 8px;

  cursor: pointer;
}

.links {
  border: 1px solid $grey;
  border-radius: 2px;
  color: $base-black-2;
  fill: $base-black-2;
  font-size: 12px;
  line-height: 16px;
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 3px 8px;

  @include darkTheme {
    color: $dark-white;
    border-color: $dark-white;

    path {
      fill: $dark-white;
    }
  }

  svg {
    width: 13px;
    height: 13px;
  }
}

.body {
  user-select: none;

  &.level {
    &_2 {
      background-color: $light-gray;
      padding: 0 16px 0 92px;

      @include darkTheme {
        background-color: $dark-bg-2;
      }
    }
  }
}
