@import 'utils/variables', 'utils/mixins';

.plus {
  color: $base-middle-gray;
  width: 18px;
  height: 18px;
  border: 2px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  @include darkTheme {
    color: $dark-white;
  }
}

.line {
  width: 8px;
  height: 2px;
  border-bottom: 2px solid;

  &_2 {
    position: absolute;
    transform: rotate(90deg);

    &.isOpen {
      transform: rotate(0deg);
    }
  }
}
