@import 'utils/variables', 'utils/mixins';

.container {
  position: relative;
  z-index: 1;
}

.label {
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 8px;
}

.search {
  height: 56px;
}

.listInner {
  @include scrollbar;
  box-shadow: 0px 8px 16px 0px rgba(9, 16, 28, 0.1), 0px 0px 4px 0px rgba(0, 0, 0, 0.07);
  width: 100%;
  max-height: 244px;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 10;
  border-radius: 4px;
  position: absolute;
  top: 100%;
}

.loader {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  @include darkTheme {
    background: $black-blue;
  }
}

.list {
  list-style: none;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  overflow-y: auto;
}

.error {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;

  @include darkTheme {
    background: $black-blue;
  }
}

.errorText {
  font-size: 13px;
  line-height: 16px;
  color: #565656;

  @include darkTheme {
    color: $white;
  }
}

.item {
  width: 100%;
  border-top: 0.5px solid $light-blue;
  border-bottom: 0.5px solid $light-blue;

  @include darkTheme {
    border-top-color: $grey;
  }
}
